
.hamburger {
    position: absolute;
    margin-top: 60px;
    right: 16px;
    transform: translate3d(0, -50%, 0);
    outline: none;
    background: none;
    border: none;
    border-radius: 0;
    width: 50px;
    height: 30px;
    padding: 0;
    display: flex;
    flex: 0 0 20%;
    z-index: 8;


    &__line {
        height: 4px;
        width: 100%;
        position: absolute;
        background-color: rgb(68, 68, 68);
    }
}

.hamburger__line_1 {
    top: 0;
    transition: .2s transform, .2s top .2s;
}

.hamburger__line_2 {
    top: calc(50% - 2px);
    transition: .2s opacity .2s;
}

.hamburger__line_3 {
    bottom: 0;
    transition: .2s transform, .2s bottom .2s;
}

.hamburger__opened {

    & .hamburger__line_1 {
        top: calc(50% - 2px);
        transform: rotate(45deg);
        rotate: (45deg);
        transition: .2s top, .2s transform .2s;
        display: block;
    }

    & .hamburger__line_2 {
        opacity: 0;
        transition: .2s opacity;
        display: block;
    }

    & .hamburger__line_3 {
        bottom: calc(50% - 2px);
        transform: rotate(-45deg);
        rotate: (-45deg);
        transition: .2s bottom, .2s transform .2s;
        display: block;
    }
}

.menu__aside {
    position: fixed;
    right: 0;
    width: calc( 500 / 1920 * 100% );
    padding-top: 60px;
    background-color: #7d9d9c;
    height: 100vh;
    transition: cubic-bezier(0.39, 0.58, 0.57, 1) 0.5s transform;
    transform: translate3d(100%, 0, 0);
    z-index: 7;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.menu__opened {
    position: fixed;
    right: 0;
    width: calc(500 / 1920 * 100%);
    padding-top: 60px;
    background-color: #7d9d9c;
    height: 100vh;
    transition: cubic-bezier(0.39, 0.58, 0.57, 1) 0.5s transform;
    transform: translate3d(-20, 0, 0);
    z-index: 7;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.aside__link {
    margin-top: 20px;
    font-size: 40px;
    font-weight: normal;
    line-height: 21px;
    color: #FFFFFF;
    margin-left: 0%;
    text-decoration: none;
    padding: 0px;
    line-height: 1.5;
    transition: .5s ease-in-out;

    &:hover {
        color: #f0ebe3;
        transform: scale(120%) ;
    }

}

.Blur {
    position: fixed;
    width: 100vw;
    height: 100vh;
    top: 0;
    left: 0;
    background-color: rgb(0 0 0 / 30%);
    backdrop-filter: blur(5px);
    -webkit-backdrop-filter: blur(5px);
    opacity: 0;
    pointer-events: none;
    transition: cubic-bezier(0.39, 0.58, 0.57, 1) 0.5s opacity;
    z-index: 2;
}

.blur_opened {
    opacity: 1;
    pointer-events: all;
}

@media screen and (max-width: 760px) and (min-width: 320px) {
    .aside__link {
        font-size: 12px;
    }

    .menu__opened {
        width: calc(300 / 1920 * 100%);
    }
}
